'use client';

import Image from 'next/image';
import Link from 'next/link';

export default function Navbar() {
  return (
    <nav className="w-full py-4 px-6 bg-gradient-to-r from-[#1E3A5F] to-[#7CA5BF] flex items-center justify-between shadow-md">
      {/* Kiri: Logo dan Nama */}
      <div className="flex items-center space-x-3">
        <Image
          src="/sarastya.jpg"
          alt="Sarastya Logo"
          width={40}
          height={40}
          className="rounded-full"
        />
        <span className="text-lg font-semibold text-white">
          PT. Sarastya Technologi
        </span>
      </div>

      {/* Kanan: Navigasi */}
      <div className="flex items-center space-x-6 text-white text-sm font-medium">
        <Link href="#tentang" className="hover:underline">Tentang</Link>
        <Link href="#role" className="hover:underline">Role Pekerjaan</Link>
        <Link href="#solusi" className="hover:underline">Solusi</Link>
        <Link href="/register" className="hover:text-yellow-300 transition-colors">Register</Link>
        <Link href="/login" className="hover:text-yellow-300 transition-colors">Login</Link>
      </div>
    </nav>
  );
}
